"use client";

import {useState, useMemo} from "react";

export default function BlogPageClient({posts}) {
  const [search, setSearch] = useState("");
  const [category, setCategory] = useState("All");
  const [author, setAuthor] = useState("All");
  const [sortOrder, setSortOrder] = useState("newest");

  const categories = useMemo(() => {
    const all = posts.flatMap(p => p.categories?.map(c => c.title) || []);
    return ["All", ...new Set(all)];
  }, [posts]);

  const authors = useMemo(() => {
    const all = posts.map(p => p.author?.name).filter(Boolean);
    return ["All", ...new Set(all)];
  }, [posts]);

  const filteredBlogs = posts
    .filter(post =>
      post.title.toLowerCase().includes(search.toLowerCase())
    )
    .filter(post =>
      category === "All"
        ? true
        : post.categories?.some(c => c.title === category)
    )
    .filter(post =>
      author === "All" ? true : post.author?.name === author
    )
    .sort((a, b) =>
      sortOrder === "newest"
        ? new Date(b.publishedAt) - new Date(a.publishedAt)
        : new Date(a.publishedAt) - new Date(b.publishedAt)
    );

  return (
    <main className="max-w-7xl mx-auto px-5 py-12">
      {/* (Your existing JSX stays almost identical) */}

      {/* Blog Cards */}
      <section>
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-10">
          {filteredBlogs.map(post => (
            <a
              key={post._id}
              href={`/blog/${post.slug}`}
              className="block border rounded-xl shadow-sm hover:shadow-md transition bg-white"
            >
              {post.mainImage && (
                <img
                  src={post.mainImage.asset?.url}
                  alt={post.title}
                  className="w-full h-48 object-cover rounded-t-xl"
                />
              )}

              <div className="p-6">
                {post.categories?.[0] && (
                  <span className="inline-block bg-blue-100 text-blue-700 text-xs px-3 py-1 rounded-full mb-3">
                    {post.categories[0].title}
                  </span>
                )}

                <h2 className="text-xl font-semibold mb-2">
                  {post.title}
                </h2>

                <p className="text-gray-600 mb-4">
                  {post.excerpt}
                </p>

                {post.author && (
                  <div className="flex items-center gap-3 mb-2">
                    {post.author.image && (
                      <img
                        src={post.author.image.asset?.url}
                        alt={post.author.name}
                        className="w-8 h-8 rounded-full"
                      />
                    )}
                    <p className="text-sm font-medium">
                      {post.author.name}
                    </p>
                  </div>
                )}

                <div className="text-sm text-gray-500 flex justify-between">
                  <span>
                    {new Date(post.publishedAt).toLocaleDateString()}
                  </span>
                </div>
              </div>
            </a>
          ))}
        </div>
      </section>
    </main>
  );
}
